.img-hover-effect {
  transition: transform 0.4s ease-in-out;
  display: block;
  margin: 0 auto;
  max-width: 100%;
  height: auto;

}

.start-project-link {
  font-size: 20px;
  color: #ffffff;
  background-color: #b88338;
  padding: 10px 20px;
  border-radius: 30px;
  cursor: pointer;
  display: inline-block;
  text-decoration: none;
  transition: transform 0.3s ease-in-out;
}

.start-project-link:hover {
  transform: scale(1.05);
}

.subtext {
  color: #555555;
  transition: color 0.3s;
  /* Smooth transition for color change */
}

.subtext:hover {
  color: #B88338;
  /* Color on hover */
}

.subtext.active {
  color: #B88338;
  /* Color on hover */
}

.web-text {
  color: #555555;
  font-size: 20px;
}

.web-title {
  color: #555555;
  font-size: 40px;
}

.option-title {
  color: #555555;
  font-size: 26px;
}

.option-text {
  color: #555555;
  font-size: 19px;
}

.offer-heading {
  color: #555555;
  font-size: 40px;
}

.service-title {
  font-size: 32px;
  color: #555555;
}

.service-desccription {
  font-size: 24px;
