.faq-answer {
  font-size: 24px !important;
  /* color: #000000; */
  /* background-color: #D9D9D9; */
  border-radius: 0px 0 15px 15px !important;
  animation: fadeIn 0.3s ease-in-out;
}

.faq-question {
  font-size: 24PX !important;
}

@media (max-width: 576px) {
  .faq-question {
    font-size: 18px !important
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(-4px);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.offer-heading {
  font-size: 40px;
  color: #555555;
}

.offer-description {
  font-size: 24px;
  color: #555555;
}

.coustum-app {
  color: #555555;
  font-size: 40px;
}

.coustum-tite {
  color: #555555;
  font-size: 24px;
}

.hero-heading {
  position: absolute;
  font-weight: 700;
  color: white;
  top: 6%;
  left: 5%;
  max-width: 90%;
  line-height: 1.3;
  z-index: 2;
  font-size: clamp(1.6rem, 5vw, 2.8rem);
  white-space: normal;
  word-break: break-word;
}

.hero-breadcrumb {
  position: absolute;
  top: 18%;
  left: 5%;
  background-color: #F0E4CD;
  color: #B88338;
  border-radius: 30px;
  /* padding: 10px 20px; */
  font-weight: 500;
  font-size: clamp(0.85rem, 2.5vw, 1.25rem);
  z-index: 2;
  max-width: 90%;
  white-space: normal;
  word-break: break-word;
}

/* === MEDIA QUERIES === */

/* Phones (up to 576px) */
@media (max-width: 576px) {
  .hero-heading {
    font-size: 1.2rem;
    top: 8%;
  }

  .hero-breadcrumb {
    font-size: 0.50rem;
    top: 40%;
  }
}

/* Tablets (577px–991px) */
@media (min-width: 577px) and (max-width: 991px) {
  .hero-heading {
    font-size: 2.5rem;
    top: 7%;
  }

  .hero-breadcrumb {
    font-size: 1rem;
    top: 18%;
  }
}

/* Large Screens (≥992px) */
@media (min-width: 992px) {
  .hero-heading {
    font-size: 2.5rem;
    top: 6%;
  }

  .hero-breadcrumb {
    font-size: 1.25rem;
    top: 17%;
  }
}


.responsive-service-img {
  max-height: 120px;
  object-fit: contain;
}

@media (max-width: 576px) {
  .responsive-service-img {
    max-height: 80px;
  }
}

@media (max-width: 768px) {
  .center-feature-img {
    max-height: 250px;
  }
}

/* Default size */
.responsive-title {
  font-size: 1.25rem;
}

.responsive-text {
  font-size: 1rem;
}

/* Smaller on mobile (≤576px) */
@media (max-width: 576px) {
  .responsive-title {
    font-size: 1rem;
  }

  .responsive-text {
    font-size: 0.875rem;
  }
}

.custom-swiper-height {
  max-height: 400px;
  /* Default for desktop/tablet */
}

@media (max-width: 576px) {
  .custom-swiper-height {
    height: 300px !important;
    max-height: 200px !important;
    /* Mobile height override */
  }
}

.center-feature-img {
  max-width: 100%;
  height: auto;
}

@media (min-width: 992px) {

  /* Desktop */
  .center-feature-img {
    max-width: 130% !important;
  }
}

.swiper-color {
  color: #555555;
}
@media (max-width: 576px) {
  .faq-question {
    font-size: 16px !important;
    padding: 10px 12px !important;
  }

  .faq-answer {
    font-size: 14px !important;
    padding: 10px 12px !important;
  }

  .faq-number {
    font-size: 16px !important;
    margin-right: 8px;
  }
}
